import Vue from 'vue'

// <input>
// <input>v-"+"
// <input v-focus placeholder="v-focus," />

Vue.directive('focus', {
  inserted (el, binding, vnode) {
    // 
    el.focus()
  }
})

// <el-input>
// <el-input>v-"+"
// <el-input v-fo placeholder="v-fo,"></el-input>

Vue.directive('fo', {
  inserted (el, binding, vnode) {
    // 
    el.querySelector('input').focus()
  }
})
