//  store
const STOREITEMDATA = 'storeitemdata' //
const STOREDRAGDATA = 'storedragdata' // 
const PROJECTTYPE = 'projecttype'
const state = {
  itemData: null, // 
  dragData: [], // 
  projectType: '' // 
}

const mutations = {
  [STOREITEMDATA]: (state, data) => {
    state.itemData = data
  },
  [STOREDRAGDATA]: (state, data) => {
    if (data) {
      state.dragData[data.index] = data.data
    } else {
      state.dragData = []
    }
  },
  [PROJECTTYPE]: (state, data) => {
    state.projectType = data
  }
}

const actions = {}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}
